import ComponentName from '../const/component'

var sequence_addon = window.xitFlowBuilderData.sequence_addon
var user_input_flow_addon = window.xitFlowBuilderData.user_input_flow_addon
const instagram_bot_addon = window.xitFlowBuilderData.instagram_addon

var hideFromContextMenu = [ComponentName.POSTBACK]

if (! sequence_addon) {
  hideFromContextMenu.push(ComponentName.SEQUENCE)
  hideFromContextMenu.push(ComponentName.SEQUENCE_SINGLE)
}

if(! user_input_flow_addon) {
  hideFromContextMenu.push(ComponentName.USER_INPUT_FLOW)
}

const config = {
  hideFromContextMenu: hideFromContextMenu,

  undroppableElements: [
    'xitFbGrip', 
    'fbDockmenu', 
    'hoverNodeCard'
  ],

  ignoreNodesWhileCheckingData: [
    ComponentName.CONDITION,
  ],
}

if (instagram_bot_addon) {
  config.ignoreNodesWhileCheckingData.push(ComponentName.CAROUSEL)
}

export default config