import Swal from 'sweetalert2'
import { lang } from '../../assets/lang'

import {
  sweetAlertCommonParams, 
  getSelectOptionsFromArrayOfObjects,
} from '../../functions/utils'

import { actionButtonActions } from '../../data'

// eslint-disable-next-line
export const actionButtonModal = async (oldData, node) => {
  const commonParams = sweetAlertCommonParams(node)
  
  return await Swal.fire({
    ...commonParams,
    title: lang.action_button_modal_title,
    html:
      '<div id="action-button-actions-group" class="button-onDemand-field form-group text-left mt-2">' + 
        '<label for="action-button-actions">' + lang.action_button_html_title_actions + '</label>' +
        '<select id="action-button-actions" class="form-control select2">' +
          '<option value="" selected>' + lang.action_button_html_title_select + '</option>' +
          getSelectOptionsFromArrayOfObjects(actionButtonActions) +
        '</select>' +
      '</div>'
    ,

    willOpen: () => {
      $('.select2').select2({ 
        width: '100%',
        dropdownParent: $('#swal2-content')
      })

      $('#action-button-actions').val(oldData.actionType || '').trigger('change')
    },

    preConfirm: () => {
      const actionType = $('#action-button-actions').val()
      const actionTypeText = $('#action-button-actions option:selected').text()

      if (! actionType) {
        Swal.showValidationMessage(lang.action_button_modal_msg_action_type)
      }

      return {
        actionType,
        actionTypeText
      }
    },    
  })
}